#wrap{
    width: 100vw;
    height: 100vh;
    background-color: #DC9696;
    background-image: url(../img/back2.png);
    background-size: 50%;
    overflow: hidden;
    position: relative;
    animation: back 90s infinite alternate linear;
}
/* header */
header{
    height: 10vh;
}
#logo{
    position: fixed;
    top: 25px;
    left: 50px;
    width: 200px;
}
.myCart{
    position: absolute;
    right: 25px;
    top: 25px;
    font-size: 3rem;
}
/* section */
section{
    display: flex;
    justify-content: center;
    height: 80vh;
    align-items: center;
}
.goods{
    width: 80%;
    display: flex;
}
.product{
    width: 50%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;
    font-weight: 600;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.1);
    margin: 0 10px;
}
.product:hover{
    background-color: rgba(255, 255, 255, 0.7);
}
.product >*{
    width: 100%;
    margin: 10px 0;
}
.product img{
    width: 70%;  
}
.product .info{
    width: 40%;
    display: flex;
    justify-content: space-between;
    font-size: 2rem;
}
/* notice */
#notice{
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgb(105, 105, 105,0.5);
}
.noticeCard{
    width: 300px;
    height: 400px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 5px 5px 6px rgb(168, 168, 168);
    text-align: center;
    line-height: 50px;
    margin: calc((100vh - 400px)/2) auto;
}
.noticeCard span{
    background-color: rgb(228, 122, 122);
    padding: 10px 30px;
    border-radius: 5px;
    font-size: 1.5rem;
    color: #fff;
}
/* aside */
aside{
    width: 320px;
    height: 100%;
    background-color: #278D98;
    position: absolute;
    right: 0;
    top: 0;
    display: flex;
    flex-wrap: wrap;
    transition: 0.3s ease-in-out;
}
aside>*{
    width: 90%;
    margin: 15px 5%;
    color: #fff;
}
.cartPart1{
    height: 5%;
    display: flex;
    justify-content: space-between;
    font-size: 1.5rem;
    font-weight: 700;
}
.cartPart2{
    height: 65%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.buyList{
    width: 99%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.buy{
    width: 90%;
    display: flex;
    align-items: center;
}
.buyPart1{
    width: 50%;
}
.buyPart1 img{
    width: 120px;
}
.buyPart2{
    width: 40%;
    line-height: 2rem;
    text-align: center;
}
.quantity{
    display: flex;
    justify-content: space-evenly;
    font-weight: 700;
}
.buyPart3{
    width: 10%;
    font-weight: 900;
}
.cartPart3{
   border-top: 3px solid #fff;
   font-size: 3rem;
   font-weight: 900;
   text-align: center;
   padding-top: 20px;
}
.cartPart3 span{
   letter-spacing: 3rem;
}
.count{
    width: 80%;   
}
.ps{
    text-align: center;
}
.ps p{
    font-size: 12px;
    border-bottom: 1px solid white;
    padding-bottom: 5px;
}
.spend{
    display: flex;
    justify-content: space-between;
    margin: 10px 0;
}
/* add */
.pointer{
    cursor: pointer;
}
.cartHide{
    transform: translateX(320px);
}
.hide{
    display: none;
}
.teal{
    color: teal;
}
.gray{
    color: rgba(177, 177, 177, 0.7);
}
.canttouch{
    pointer-events: none;
}
/* animate */
@keyframes back{
    0%{
        background-position: 0 0;
    }
    100%{
        background-position: 1000% 1000%; 
    }
}

@media (max-width:760px){
    /* header */
    header{
        height: 5vh;
    }
    #logo{
        top: 5px;
        width: 150px;
    }
    .myCart{
        font-size: 2rem;
    }
    /* section */
    section{
        height: 90vh;
    }
    .goods{
        width: 250px;
        height: 90%;
        flex-wrap: wrap;
    }
    .product{
        width: 100%;
        margin: 5px 10px;
        height: 50%;
    }
    .product >*{
        margin: 2px 0;
    }
    .product h2, .product .info{
        font-size: 1rem;
    }
    .product p{
        font-size: 0.5rem;
    }
    .product img{
        width: 180px;
        height: 180px;  
    }
    
}